.footer {
     width: 100%;
     height: 250px;
     margin-top: auto;
     display: flex;
     justify-content: space-between;
     align-items: center;
     background-color: #282c3f;
     color: white;
     padding: 50px 0px;
     bottom: 0;
   }
   
   .footer__logo {
     flex-grow: 0.3;
     display: flex;
     align-items: center;
     height: 100%;
     padding: 0 30px;
   }
   
   .footer__image {
     height: 60px;
     object-fit: cover;
     width: 60px;
     border-radius: 50%;
   }
   
   .footer__feedback {
     flex-grow: 0.3;
     padding: 0px 50px;
     height: 100%;
     display: flex;
     flex-direction: column;
     justify-content: center;
     font-size: 17px;
   }
   
   .footer__connect {
     flex-grow: 0.4;
     height: 100%;
     /* padding: 0 30px; */
     display: flex;
     flex-direction: column;
     font-size: 17px;
   }
   
   .footer__title {
     font-size: 40px;
     font-weight: 600;
     margin-left: 20px;
   }
   
   .footer__heading {
     font-size: 30px;
     font-weight: 500;
   }
   
   .footer__social {
     display: flex;
     align-items: center;
     padding-top: 8px;
     padding-bottom: 4px;
   }
   
   .footer__social:hover {
     cursor: pointer;
   }
   
   .footer__social .social {
     padding-left: 5px;
   }
   
   .footer__heading__links {
     display: flex;
     flex-direction: column;
     padding-top: 10px;
   }
   
   .footer__header__link {
     color: #ffdf88;
     text-decoration: none;
   }
   
   .footer__header__link:hover {
     color: #ffc72c;
   }
   
   @media (max-width: 1070px) {
     .footer__logo {
       display: none;
     }
   
     .footer__connect {
       flex: 0.4;
       padding-left: 70px;
     }
   
     .footer__feedback {
       flex: 0.6;
       border-right: 1px solid white;
     }
   }
   
   @media (max-width: 570px) {
     .footer {
       display: flex;
       flex-direction: column;
     }
   
     .footer__feedback {
       padding-bottom: 30px;
       padding-left: 70px;
       padding-right: 35px;
       border: none;
       margin-right: auto;
     }
   
     .footer__connect {
       margin-right: auto;
       justify-content: start;
       padding-left: 70px;
       padding-right: 35px;
     }
   }
   